html {
  font-size: 16px;
}

@media screen and (max-width: 600px) {
  html {
    font-size: 19px;
  }
}

body {
  margin: 0;
  padding: 0;
  color: #242628;
  font-family: Tahoma, Verdana, Segoe, sans-serif;
  text-align: center;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
}

div,
article {
  display: block;
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6,
.like-h1,
.like-h2,
.like-h3,
.like-h4,
.like-h5,
.like-h6 {
  font-family: Palatino, "Palatino Linotype", "Palatino LT STD", "Book Antiqua", Georgia, serif;
  line-height: 1.2em;
  margin: .6em 0;
}

h1,
.like-h1 {
  font-size: 2.5rem;
}

h2,
.like-h2 {
  font-size: 2rem;
}

h3,
.like-h3 {
  font-size: 1.6rem;
}

h4,
.like-h4 {
  font-size: 1.4rem;
}

h5,
.like-h5 {
  font-size: 1.2rem;
}

h6,
.like-h6 {
  font-size: 1.2rem;
  font-weight: 400;
}

ul {
  list-style-type: square;
  margin: 0;
  padding: 5px 0 5px 1em;
}

ol {
  list-style: none;
  counter-reset: ol;
  padding: 5px 0 5px 15px;
}

ol li::before {
  content: counters(ol, ".") ".";
  counter-increment: ol;
  margin-right: .5em;
}

p {
  font-size: 1rem;
  line-height: 1.4em;
}

@media screen and (max-width: 600px) {
  p {
    line-height: 1.2em;
  }
}

p a {
  margin-left: .3em;
  margin-right: .3em;
}

em {
  font-style: italic;
}

strong {
  font-weight: 700;
}

a {
  background-image: linear-gradient(to bottom, #fff 0%, #4a90e2 1%);
  background-size: 1px 1px;
  background-position: 0 90%;
  background-repeat: repeat-x;
  color: #4a90e2;
  transition: color 200ms, background-image 200ms;
  text-decoration: none;
}

a:hover {
  background-image: linear-gradient(to bottom, #fff 0%, #2e77cc 1%);
  background-size: 1px 1px;
  background-position: 0 90%;
  background-repeat: repeat-x;
  color: #2e77cc;
}

code,
pre {
  font-family: monaco, Consolas, "Lucida Console", monospace;
  border-radius: 4px;
  color: #242628;
  background: #eaeaea;
}

code {
  padding: 0.1em 0.4em;
}

pre {
  display: block;
  padding: 0;
}

pre code {
  display: block;
  padding: 15px;
  overflow-x: auto;
}

a,
button {
  cursor: pointer;
}

.ta-left {
