}

.ta-right {
  text-align: right;
}

.ta-center {
  text-align: center;
}

.text-italic {
  font-style: italic;
}

.text-bold {
  font-weight: bold;
}

.text-underline {
  background-image: linear-gradient(to bottom, #fff 0%, #242628 1%);
  background-size: 1px 1px;
  background-position: 0 90%;
  background-repeat: repeat-x;
}

.text-slash {
  background-image: linear-gradient(to bottom, #fff 0%, #242628 1%);
  background-size: 1px 1px;
  background-position: 0 50%;
  background-repeat: repeat-x;
}

.flexMenu {
  display: inline-block;
  position: fixed;
  top: 0;
  height: 100vh;
  width: 100%;
  max-width: 300px;
  background: #fff;
  overflow-x: hidden;
  overflow-y: scroll;
  text-align: left;
  z-index: 100;
}

@media screen and (max-width: 900px) {
  .flexMenu {
    transition: transform 300ms;
  }
  .flexMenu.-left {
    transform: translateX(-100%);
  }
  .flexMenu.-right {
    transform: translateX(100%);
  }
  .flexMenu.is-open {
    transform: translateX(0);
  }
}

.flexMenu.-left {
  left: 0;
  border-right: 1px solid #ccc;
}

.flexMenu.-right {
  right: 0;
  border-left: 1px solid #ccc;
}

.flexMenu a {
  display: block;
  background-image: none;
}

.flexMenu_nav {
  display: block;
}

.flexMenuToggleButton {
  position: fixed;
  top: 15px;
  left: 15px;
  right: 15px;
}

@media screen and (min-width: 900px) {
  .flexMenuToggleButton {
    display: none;
  }
}

.flexMenuToggleButton.-left {
  right: auto;
}

.flexMenuToggleButton.-right {
  left: auto;
}

.flexMenu_block {
  padding: 0 15px;
  z-index: 101;
}

.flexMenu_block a {
  display: block;
}

.flexMenu_block.-logo {
  padding: 0;
}

.flexMenu_block.-logo > a {
  transition: color 300ms, background 300ms;
  padding: 0 15px;
  text-decoration: none;
  background-image: none;
  color: #242628;
}

.flexMenu_block.-logo > a:hover {
  color: #2e77cc;
  background: #eaeaea;
}

.flexMenu_block_item {
  display: block;
  position: relative;
}

@media screen and (min-width: 900px) {
  .flexMenu:not([data-rig-ui-menu=horizontal]).-left ~ .container {
    padding-left: 300px;
  }
  .flexMenu:not([data-rig-ui-menu=horizontal]).-right ~ .container {
    padding-right: 300px;
  }
}

@media screen and (min-width: 1400px) {
  .flexMenu:not([data-rig-ui-menu=horizontal]).-left ~ .container {
    padding-left: 150px;
  }
  .flexMenu:not([data-rig-ui-menu=horizontal]).-right ~ .container {
    padding-right: 150px;
  }
}

@media screen and (min-width: 1500px) {
  .flexMenu:not([data-rig-ui-menu=horizontal]).-left ~ .container {
    padding-left: 100px;
  }
  .flexMenu:not([data-rig-ui-menu=horizontal]).-right ~ .container {
    padding-right: 100px;
  }
}

@media screen and (min-width: 1700px) {
  .flexMenu:not([data-rig-ui-menu=horizontal]).-left ~ .container {
    padding-left: 0;
  }
  .flexMenu:not([data-rig-ui-menu=horizontal]).-right ~ .container {
    padding-right: 0;
  }
}

@media screen and (min-width: 900px) {
  .flexMenu[data-rig-ui-menu=horizontal] {
    display: block;
    position: relative;
    height: auto;
    max-width: 100%;
    border: 0;
    border-bottom: 1px solid #ccc;
    overflow: visible;
  }
  .flexMenu[data-rig-ui-menu=horizontal].-sticky {
    position: fixed;
  }
  .flexMenu[data-rig-ui-menu=horizontal].-sticky + .container {
    margin-top: 4em;
  }
  .flexMenu[data-rig-ui-menu=horizontal] .flexMenu_nav {
    display: table;
    width: 100%;
  }
  .flexMenu[data-rig-ui-menu=horizontal] .flexMenu_block {
    display: table-cell;
    vertical-align: middle;
    height: auto;
    text-align: left;
  }
  .flexMenu[data-rig-ui-menu=horizontal] .flexMenu_block.-left,
  .flexMenu[data-rig-ui-menu=horizontal] .flexMenu_block.-right {
    width: 1%;
  }
  .flexMenu[data-rig-ui-menu=horizontal] .flexMenu_block.-left {
    margin-left: 0;
  }
  .flexMenu[data-rig-ui-menu=horizontal] .flexMenu_block.-right {
    margin-right: 0;
    text-align: right;
  }
  .flexMenu[data-rig-ui-menu=horizontal] .flexMenu_block_item {
    display: inline-block;
    padding: 0 15px;
  }
  .flexbox .flexMenu[data-rig-ui-menu=horizontal] .flexMenu_nav {
    display: flex;
    justify-content: space-between;
  }
  .flexbox .flexMenu[data-rig-ui-menu=horizontal] .flexMenu_block {
    display: flex;
    flex-direction: row;
    flex-grow: 1;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  .flexbox .flexMenu[data-rig-ui-menu=horizontal] .flexMenu_block.-left,
  .flexbox .flexMenu[data-rig-ui-menu=horizontal] .flexMenu_block.-right {
    width: auto;
    flex-grow: 0;
  }
  .flexbox .flexMenu[data-rig-ui-menu=horizontal] .flexMenu_block_item {
    display: flex;
  }
  .flexbox .flexMenu[data-rig-ui-menu=horizontal] ul.flexMenu_block {
    flex-flow: row wrap;
    justify-content: flex-start;
  }
}

[data-dropdown-element=dropdown] {
  display: none;
  padding-left: 7.5px;
  text-align: left;
  z-index: 110;
}

[data-dropdown-element=toggle] {
  position: relative;
}

[data-dropdown-element=toggle].is-open ~ [data-dropdown-element=dropdown],
[data-dropdown-element=toggle].is-open > [data-dropdown-element=dropdown] {
  display: block;
  pointer-events: auto;
  max-height: 50vh;
}

@media screen and (min-width: 900px) {
  [data-rig-ui-menu=horizontal] [data-dropdown-element=dropdown] {
    display: block;
    position: absolute;
    top: 100%;
    left: 50%;
    padding: 7.5px;
    transform-origin: 50% 0;
    transition: transform 300ms cubic-bezier(0.34, 1.61, 0.7, 1), opacity 300ms;
    transform: translate(-50%, 10px) scale3d(0, 0, 0);
    background: #fff;
    border-radius: 4px;
    border: 1px solid #ccc;
    opacity: 0;
  }
  [data-rig-ui-menu=horizontal] [data-dropdown-element=dropdown]::before,
  [data-rig-ui-menu=horizontal] [data-dropdown-element=dropdown]::after {
    content: ' ';
    position: absolute;
    top: 0;
    left: 50%;
    border: 10px solid transparent;
    border-bottom-color: #ccc;
    transform: translate(-50%, -100%);
  }
  [data-rig-ui-menu=horizontal] [data-dropdown-element=dropdown]::after {
    top: 2px;
    border-bottom-color: #fff;
  }
  [data-rig-ui-menu=horizontal] [data-dropdown-element=toggle]::after {
    content: none;
  }
  [data-rig-ui-menu=horizontal] [data-dropdown-element=toggle].is-open ~ [data-dropdown-element=dropdown] {
    display: block;
    opacity: 1;
    transform: translate(-50%, 10px) scale3d(1, 1, 1);
  }
}

