input[type="email"],
input[type="number"],
input[type="password"],
input[type="search"],
input[type="tel"],
input[type="text"],
input[type="url"],
input[type="color"],
input[type="date"],
input[type="datetime"],
input[type="datetime-local"],
input[type="month"],
input[type="time"],
input[type="week"],
textarea,
select {
  display: block;
  width: 100%;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  transition: border 200ms;
}

input[type="checkbox"],
input[type="radio"] {
  cursor: pointer;
}

input[type="email"]:hover,
input[type="number"]:hover,
input[type="password"]:hover,
input[type="search"]:hover,
input[type="tel"]:hover,
input[type="text"]:hover,
input[type="url"]:hover,
input[type="color"]:hover,
input[type="date"]:hover,
input[type="datetime"]:hover,
input[type="datetime-local"]:hover,
input[type="month"]:hover,
input[type="time"]:hover,
input[type="week"]:hover,
textarea:hover,
select:hover {
  border: 1px solid #999;
}

input[type="email"]:focus,
input[type="number"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
input[type="tel"]:focus,
input[type="text"]:focus,
input[type="url"]:focus,
input[type="color"]:focus,
input[type="date"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="month"]:focus,
input[type="time"]:focus,
input[type="week"]:focus,
textarea:focus,
select:focus {
  outline: none;
  box-shadow: none;
  border: 1px solid #999;
}

.label {
  display: block;
  position: relative;
  width: 100%;
  padding: 15px 0;
  box-sizing: border-box;
}

.label_title {
  display: block;
}

input[type="email"],
input[type="number"],
input[type="password"],
input[type="search"],
input[type="tel"],
input[type="text"],
input[type="url"],
input[type="color"],
input[type="date"],
input[type="datetime"],
input[type="datetime-local"],
input[type="month"],
input[type="time"],
input[type="week"] {
  height: 3em;
  line-height: 3em;
  padding: 0 7.5px;
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  height: 3em;
  line-height: 3em;
  padding: 0 7.5px;
  background: #fff;
  background-image: url("data:image/svg+xml;base64,");
  background-size: 15px;
  background-repeat: no-repeat;
  background-position: right 7.5px center;
}

textarea {
  resize: vertical;
  padding: .7em;
  line-height: 1.4em;
  min-height: 15.4em;
}

.label.-checkbox,
.label.-radio {
  padding-left: 1.5em;
  cursor: pointer;
  user-select: none;
}

.label.-checkbox input[type="checkbox"],
.label.-checkbox input[type="radio"],
.label.-radio input[type="checkbox"],
.label.-radio input[type="radio"] {
  position: absolute;
  top: 50%;
  left: 0;
  margin: 0;
  transform: translateY(-50%);
}

.label.-checkbox input[type="checkbox"] {
  border-radius: 10%;
}

.label.-radio input[type="radio"] {
  border-radius: 50%;
}

.radiogroup .label {
  padding-top: 7.5px;
  padding-bottom: 7.5px;
}

.radiogroup.-horizontal .label {
  padding: 7.5px 15px;
  text-align: center;
}

.radiogroup.-horizontal input[type="radio"] {
  position: relative;
}

.switchbutton {
  display: inline-block;
  position: relative;
  font-size: 0;
  user-select: none;
}

.switchbutton .label {
  display: inline-block;
  position: relative;
  width: 120px;
  padding: 0;
  line-height: 30px;
  font-size: 1rem;
}

.switchbutton .label.-on {
  padding-right: 40px;
  text-align: right;
}

.switchbutton .label.-off {
  padding-left: 40px;
  text-align: left;
}

.switchbutton .label_title {
  display: block;
}

.switchbutton input[type="radio"],
.switchbutton input[type="checkbox"] {
  position: absolute;
  visibility: hidden;
}

.switchbutton_base {
  position: absolute;
  top: 0;
  left: 50%;
  height: 30px;
  width: 60px;
  background: #ddd;
  transform: translateX(-30px);
  pointer-events: none;
  border-radius: 15px;
}

.switchbutton_marker {
  display: inline-block;
  position: absolute;
  left: 50%;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  border: 2px solid #c4c4c4;
  background: #fff;
  transform: translateX(-100%);
  transition: transform 200ms ease-in;
  pointer-events: none;
}

.switchbutton.-radio .switchbutton_base {
  background: #96d992;
}

.switchbutton.-radio input.on:checked ~ .switchbutton_marker {
  transform: translateX(-100%);
}

.switchbutton.-radio input.off:checked ~ .switchbutton_marker {
  transform: translateX(0);
}

.switchbutton.-checkbox .switchbutton_base::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 30px;
  width: 60px;
  background: #96d992;
  border-radius: 15px;
  transform-origin: 50% 50%;
  transform: scale3d(0, 0, 0);
  transition: transform 200ms ease-in;
}

.switchbutton.-checkbox input:not(:checked) ~ .switchbutton_marker {
  transform: translateX(-100%);
}

.switchbutton.-checkbox input:checked ~ .switchbutton_marker {
  transform: translateX(0);
}

.switchbutton.-checkbox input:checked ~ .switchbutton_base::after {
  transform: scale3d(1, 1, 1);
}

.btn {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  display: inline-block;
  background: #f5f5f5;
  border: 1px solid #ccc;
  color: #242628;
  border-radius: 4px;
  line-height: 2em;
  font-size: 1rem;
  margin-bottom: 15px;
  padding: 0 0.5em;
  text-align: center;
}

.btn:focus {
  outline: 0;
}

.btn:hover {
  background: gainsboro;
}

.btn[type="submit"] {
  background: #96d992;
  border-color: #84d27f;
}

.btn[type="submit"]:hover {
  background: #8bd587;
}

.btn[type="cancel"] {
  background: #d2494d;
  color: #fff;
  border-color: #cd3539;
}

.btn[type="cancel"]:hover {
  background: #cf3d41;
}

.btnGroup {
  font-size: 0;
}

.btnGroup .btn {
  border-radius: 0;
  padding-left: .5em;
  padding-right: .5em;
}

.btnGroup .btn:not(:first-child) {
  border-left: 0;
}

.btnGroup .btn:first-child {
  border-radius: 4px 0 0 4px;
}

.btnGroup .btn:last-child {
  border-radius: 0 4px 4px 0;
}
